.end-card {
    --border: #e3e3e3;
    --text: #1f1f1f;
    --muted: #8a8a8a;
    --bg: #fff;
    --pass: #1a7f37;
    --fail: #d92d20;
    --primary: #3b82f6;

    background: var(--bg);
    border: 2px solid var(--border);
    border-radius: 16px;
    padding: 24px;
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.06);
    font-size: 62px;
    /* 요구: 기본 32px */
    color: var(--text);
}

.end-header {
    display: grid;
    grid-template-columns: 1fr auto;
    align-items: center;
    gap: 20px;
    border-bottom: 2px dashed var(--border);
    padding-bottom: 16px;
}

.end-title {
    font-size: 72px;
    font-weight: 800;
    margin: 1rem 2rem;
    color: #d92d20;
}

.end-score {
    font-weight: 700;
    margin: 1rem 2rem;
}

.end-score__value {
    font-variant-numeric: tabular-nums;
    display: inline-block;
    min-width: 3ch;
    margin: 0 8px;
    transform-origin: 50% 60%;
}

/* 집계 중 스코어 팝 애니메이션: 2초 동안 1 -> 1.18 -> 1 로 복귀 */
@keyframes scorePop {
    0% {
        transform: scale(1);
        filter: none;
    }

    45% {
        transform: scale(1.18);
        filter: drop-shadow(0 4px 10px rgba(0, 0, 0, 0.12));
    }

    100% {
        transform: scale(1);
        filter: none;
    }
}

.end-card.counting .end-score__value.animating {
    animation: scorePop 2s both;
}

/* 집계 끝: 정지(원래 사이즈 유지) */
.end-card.done .end-score__value.settled {
    transform: scale(1);
    filter: none;
}

/* 합격/불합격 배지: 집계 완료 후에만 페이드 인 + 살짝 팝 */
@keyframes badgeIn {
    0% {
        opacity: 0;
        transform: translateY(-6px) scale(0.96);
    }

    100% {
        opacity: 1;
        transform: translateY(0) scale(1);
    }
}

.end-status {
    display: inline-block;
    margin: 1rem 1.5rem;
    padding: 6px 14px;
    font-size: 42px;
    font-weight: 800;
    letter-spacing: 0.02em;
    opacity: 0;
    transform: translateY(-6px) scale(0.96);
    z-index: 1000;
}

.end-card.done .end-status {
    animation: badgeIn .28s ease-out forwards;
}

.end-status.pass {
    color: var(--pass);
    border-bottom: 6px solid #1a7f37;
}

.end-status.fail {
    color: var(--fail);
    border-bottom: 6px solid #d92d20;
}

.end-actions {
    display: flex;
    gap: 15px;
}

.btn {
    font-size: 32px;
    line-height: 1;
    margin: 0 0.5rem;
    padding: 14px 18px;
    border-radius: 12px;
    border: 2px solid var(--border);
    background: rgba(255, 255, 255, 0.3);
    cursor: pointer;
    transition: transform .1s ease, box-shadow .1s ease, background .1s ease;
}

.btn:hover {
    transform: translateY(-1px);
}

.btn:active {
    transform: translateY(0);
}

.btn--primary {
    background: rgba(255, 255, 255, 0.3);
    ;
    color: #222222;
    border-color: #ffffff;
}

.end-section-title {
    font-size: 42px;
    font-weight: 700;
    margin: 1.5rem 2rem;
}

.end-wrong {
    padding-top: 16px;
}

.end-empty {
    font-size: 48px;
    color: #d92d20;
}

/* 틀린 문제 버튼 그리드 */
.end-wrong__grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(88px, 1fr));
    gap: 10px;
    margin-left: 0.5rem;
}

.num-btn {
    border: none;
    font-size: 32px;
    display: inline-block;
    align-items: center;
    justify-content: center;
    height: 50px;
    width: 50px;
    border-radius: 12px;
    border-bottom: 2px solid #d92d20;
    background: rgba(255, 255, 255, 0.5);
    ;
    box-shadow: -3px -3px 15px rgba(0, 0, 0, 0.5);
    cursor: pointer;
    font-weight: 800;
    transition: background .12s ease, transform .12s ease, box-shadow .12s ease;
}

.num-btn:hover {
    background: #f2f6ff;
    transform: translateY(-1px);
}

.num-btn:active {
    transform: translateY(0);
}

.end-scroll {
    margin-top: 18px;
    max-height: 0;
    opacity: 0;
    transform: translateY(8px);
    overflow: hidden;
    transition: max-height .6s ease, opacity .6s ease, transform .6s ease;
}

.end-scroll.show {
    max-height: 260px;
    /* 펼쳐지는 높이 */
    opacity: 1;
    transform: translateY(0);
}

.scroll-wrap {
    background-color: rgba(255,255,255,0.4);
    display: flex;
    align-items: center;
justify-content: center;
    position: relative;
    padding: 22px 28px;
    border: 1px solid rgba(154, 126, 76, .80);
    border-radius: 10px;
    box-shadow:
        inset 0 0 0 1px rgba(255, 255, 255, .4),
        10px 10px 18px rgba(0, 0, 0, .10);

    /* 두루마리 가장자리(둥근 막대) */
    &::before,
    &::after {
        content: "";
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 16px;
        height: calc(100% + 18px);
        border-radius: 12px;
        background: linear-gradient(180deg, #e0d2b4, #cdbb93);
        box-shadow:
            inset 0 0 0 1px rgba(255, 255, 255, .5),
            0 6px 12px rgba(0, 0, 0, .15);
    }

    &::before {
        left: -10px;
    }

    &::after {
        right: -10px;
    }
}

/* 스크롤 문구: 페이드 인 업 + 스태거 */
@keyframes fadeUp {
    from {
        opacity: 0;
        transform: translateY(6px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.scroll-line {
    margin: 4px 4rem;
    font-size: 48px;
    /* 네 전체 폰트 스케일에 맞춰 필요시 조정 */
    font-weight: 800;
    color: #1b1b1b;
    opacity: 0;
    animation: fadeUp .5s ease forwards;
}

/* 합격/불합격에 따라 포인트 색 살짝 */
.end-scroll.pass .scroll-line:first-child {
    color: #1b1b1b;
}

.end-scroll.fail .scroll-line:first-child {
    color: #b91c1c;
}