.omr-card{font-family:ui-sans-serif,system-ui,-apple-system,"Segoe UI",Roboto,"Noto Sans KR",Arial,"Apple SD Gothic Neo","맑은 고딕",sans-serif;font-style:normal;line-height:1.25;font-size:32px;background:hsla(0,0%,100%,.6)}.omr-options{background:none;border:1px solid #d7d7d7;border-radius:10px;padding:8px 10px}.omr-row{display:flex;align-items:center;gap:12px;min-height:48px;padding:4px 6px;border-bottom:1px dashed #eaeaea}.omr-row:last-child{border-bottom:0}.omr-mark{appearance:none;-webkit-appearance:none;width:26px;height:26px;border:2px solid #1f1f1f;border-radius:50%;display:inline-grid;place-items:center;background:#fff;vertical-align:middle;transform:translateY(1px);margin:0}.omr-mark::after{content:"";width:16px;height:16px;border-radius:50%;transform:scale(0);transition:transform 120ms ease;background:#1f1f1f}.omr-mark:checked::after{transform:scale(1)}.omr-mark:focus-visible{outline:3px solid rgba(33,150,243,.35);outline-offset:2px}.omr-label{font-style:normal;line-height:1;translate:0 1px;user-select:none}.omr-text{font-size:24px;line-height:1.25}@media(max-width: 480px){.omr-row{min-height:44px}.omr-mark{width:24px;height:24px;transform:translateY(1px)}.omr-mark::after{width:14px;height:14px}}